import React, { useEffect, useRef, useState } from 'react';

import * as styles from './howItWorks.module.css';

// const steps = [
// 	<div>Choose your AI</div>,
// 	<div>API Plugin</div>,
// 	<div>
// 		Track Portfolio
// 		<br />
// 		&amp; earn profits
// 	</div>,
// ];

export default function HowItWorks(props) {
	const [activeSlideIndex, setActiveSideIndex] = useState(0);

	const hiwCntnrElmRef = useRef(null);
	const hiwUlRef = useRef(null);
	// const hiwCntnrElmHeightRef = useRef(null);
	// const viewportHeightRef = useRef(null);

	const _getPercentageScrolled = (container, totalScrollDistance) => {
		const containerTopEdgeDist = container.getBoundingClientRect().top; //top edge of container relative to viewport top edge
		if (containerTopEdgeDist > 0) return 0; //null; //since we are only interested when top edge scrolls past/above viewport top edge. ie when it is negative
		if (containerTopEdgeDist * -1 > totalScrollDistance) return 100; //null;
		// console.log('container top relative to viewport:',containerTopEdgeDist);

		// containerTopEdgeDist *= -1; //convert negative number into positive

		// Calculate the percentage scrolled
		let scrolledDistance = containerTopEdgeDist * -1; //viewportHeight - containerTopEdgeDist;
		if (scrolledDistance < 0) scrolledDistance = 0;
		if (scrolledDistance > totalScrollDistance) scrolledDistance = totalScrollDistance;

		// const scrollPercent = (scrolledDistance / totalScrollDistance) * 100;
		// console.log(`Scrolled percentage: ${scrollPercent.toFixed(2)}%`);
		// return (scrolledDistance / totalScrollDistance) * 100;
		return Math.ceil((scrolledDistance / totalScrollDistance) * 100);
	};

	const handleScrollProgress = (container, totalScrollDistance, prcntPartPerSlide, numSlides) => {
		const prcnt = _getPercentageScrolled(container, totalScrollDistance);
		// console.log('prcnt', prcnt);
		// if(!prcnt) return;
		// if(prcnt == 0 || prcnt == 100)return;
		// console.log(`Scrolled percentage: ${prcnt.toFixed(2)}%`);

		// progressBar.style.height = prcnt+'%';

		//here depending on percentage scrolled, make relevant slides active (ie add/remove css active class)
		// console.log('Active slide page:', Math.ceil(prcnt / prcntPartPerSlide));

		///////
		// const scrollToSlideIndex = Math.ceil(prcnt / prcntPartPerSlide) - 1;
		// const scrollToSlideIndex = prcnt > 95 ? numSlides - 1 : Math.ceil(prcnt / prcntPartPerSlide) - 1;
		const scrollToSlideIndex = prcnt <= 1 ? 0 : prcnt > 95 ? numSlides - 1 : Math.ceil(prcnt / prcntPartPerSlide) - 1;
		// if (slides[scrollToSlideIndex].classList.contains('active')) return;
		// container.getElementsByClassName('active')[0].classList.remove('active');
		// slides[scrollToSlideIndex].classList.add('active');
		setActiveSideIndex(scrollToSlideIndex);
	};

	useEffect(() => {
		if (hiwCntnrElmRef.current && hiwUlRef.current) {
			// hiwCntnrElmHeightRef.current = hiwCntnrElmRef.current.getBoundingClientRect().height;
			// viewportHeightRef.current = window.innerHeight;
			const containerHeight = hiwCntnrElmRef.current.getBoundingClientRect().height;
			const viewportHeight = window.innerHeight;

			// Calculate the total distance that needs to be scrolled for the entire container to be revealed
			const totalScrollDistance = containerHeight - viewportHeight; // + viewportHeight;

			// const progressBar = hiwCntnrElmRef.current.getElementsByClassName('progressBarV')[0].firstChild;
			// const slides = hiwUlRef.current.children; //hiwCntnrElmRef.current.getElementsByClassName('hiwUl')[0].children;
			const numSlides = hiwUlRef.current.children.length; //slides.length;
			if (numSlides > 1) {
				const prcntPartPerSlide = 100 / numSlides; //how much each page refers to in percentage

				//scheduled to be run on mount
				handleScrollProgress(hiwCntnrElmRef.current, totalScrollDistance, prcntPartPerSlide, numSlides);

				//schedule to be run on scroll
				window.addEventListener(
					'scroll',
					() => {
						handleScrollProgress(hiwCntnrElmRef.current, totalScrollDistance, prcntPartPerSlide, numSlides);
					},
					false,
				);

				return () => {
					window.removeEventListener('scroll', handleScrollProgress);
				};
			}
		}
	}, []);

	return (
		<div ref={hiwCntnrElmRef} className={styles.hiwCC} {...props}>
			<div className={styles.hiwC}>
				<ul ref={hiwUlRef} className={styles.hiwUl}>
					<li className={activeSlideIndex === 0 ? styles.active : null}>
						<div>Choose your AI</div>
					</li>
					<li className={activeSlideIndex === 1 ? styles.active : null}>
						<div>Deposit Funds</div>
					</li>
					<li className={activeSlideIndex === 2 ? styles.active : null}>
						<div>API Plugin</div>
					</li>
					<li className={activeSlideIndex === 3 ? styles.active : null}>
						<div>
							Track Portfolio
							<br />
							&amp; earn profits
						</div>
					</li>
				</ul>
			</div>
		</div>
	);
}
